export class Shadow {
    constructor(el) {
        window.addEventListener('mousemove', this.shadow);
    }

    shadow(e) {
        const walk = 100;
        const { outerWidth: width, outerHeight: height } = this;
        let { offsetX: x, offsetY: y } = e;

        if (this != e.target) {
            x = x + e.target.offsetLeft;
            y = y + e.target.offsetTop;
        }

        const xWalk = Math.round((x / width) * walk - walk / 2) / 10;
        const yWalk = Math.round((y / height) * walk - walk / 2) / 10;

        document.querySelector(
            '.header__title'
        ).style.textShadow = `${xWalk}px ${yWalk}px 1rem rgba(49, 49, 49, .5)`;
    }
}
